import React from 'react';
import { BrowserRouter as Router, Route, Switch, Redirect } from 'react-router-dom';
import { ITwitchExtensionPrimitiveCSV } from '../components/types';
import {
  cardBody,
  statisticStyle,
  gridStyle,
  graphCardStyle,
  gridStyleStatisc
} from '../components/style';

import { OverViewBox } from './overview';
import { MonetizationGraphBox } from './monetization-graph';
import { InstallationsGraphBox } from './installations-graph';
import { InterationGraphBox } from './interation-graph';
import { LiveActivatedChannelsBox } from './live-activated-channels';
import { Empty, PageHeader, Button } from 'antd';
import { ImportTwitchCSV } from '../components';

interface IProps {
  csv: ITwitchExtensionPrimitiveCSV[];
  initialDateIndex: number;
  lastDateIndex: number;
  handleFileChosen: (file: any) => void;
  handlePushhistory: (url: string) => void;
  uploadCSV: boolean;
}
const panels = ({
  csv,
  initialDateIndex,
  lastDateIndex,
  handleFileChosen,
  uploadCSV,
  handlePushhistory
}: IProps): JSX.Element => {
  return (
    <Router>
      <Switch>
        <Route
          exact
          path='/upload-csv'
          component={({ history }: any) => (
            <Empty
              style={{ height: '75vh', margin: 20, padding: 30 }}
              description={
                <>
                  <span>
                    Please click the button to import your CSV file <b> ( V2+ ) </b>
                  </span>
                </>
              }>
              <PageHeader
                title='CSV File'
                subTitle={<ImportTwitchCSV handleFileChosen={handleFileChosen} />}
                footer={
                  <Button
                    disabled={!uploadCSV}
                    icon='dashboard'
                    onClick={() => {
                      history.push('/overview');
                      handlePushhistory('overview');
                    }}>
                    Overview
                  </Button>
                }
              />
            </Empty>
          )}
        />
        <Redirect exact from='/' to='/upload-csv' />
        <Route
          exact
          path='/overview'
          component={() =>
            csv.length ? (
              <OverViewBox
                cardBody={cardBody}
                gridStyleStatisc={gridStyleStatisc}
                statisticStyle={statisticStyle}
                gridStyle={gridStyle}
                initialDateIndex={initialDateIndex}
                lastDateIndex={lastDateIndex}
                csv={csv}
              />
            ) : (
              <Redirect to='/upload-csv' />
            )
          }
        />
        <Route
          exact
          path='/installations'
          component={() =>
            csv.length ? (
              <InstallationsGraphBox
                graphCardStyle={graphCardStyle}
                gridStyleStatisc={gridStyleStatisc}
                statisticStyle={statisticStyle}
                initialDateIndex={initialDateIndex}
                lastDateIndex={lastDateIndex}
                csv={csv}
              />
            ) : (
              <Redirect to='/upload-csv' />
            )
          }
        />
        <Route
          exact
          path='/monetization'
          component={() =>
            csv.length ? (
              <MonetizationGraphBox
                graphCardStyle={graphCardStyle}
                gridStyleStatisc={gridStyleStatisc}
                statisticStyle={statisticStyle}
                initialDateIndex={initialDateIndex}
                lastDateIndex={lastDateIndex}
                csv={csv}
              />
            ) : (
              <Redirect to='/upload-csv' />
            )
          }
        />
        <Route
          exact
          path='/iteraction'
          component={() =>
            csv.length ? (
              <InterationGraphBox
                graphCardStyle={graphCardStyle}
                gridStyleStatisc={gridStyleStatisc}
                statisticStyle={statisticStyle}
                initialDateIndex={initialDateIndex}
                lastDateIndex={lastDateIndex}
                csv={csv}
              />
            ) : (
              <Redirect to='/upload-csv' />
            )
          }
        />
        <Route
          exact
          path='/activated'
          component={() =>
            csv.length ? (
              <LiveActivatedChannelsBox
                graphCardStyle={graphCardStyle}
                gridStyleStatisc={gridStyleStatisc}
                statisticStyle={statisticStyle}
                clientID={csv[0]['Extension Client ID']}
              />
            ) : (
              <Redirect to='/upload-csv' />
            )
          }
        />
        <Route component={() => <Redirect to='/upload-csv' />} />
      </Switch>
    </Router>
  );
};
export const ContentBox = (props: IProps): JSX.Element => panels(props);
